#spinner {
    border: 10px solid #f3f3f3; 
    border-top: 10px solid rgb(145, 231, 16);; 
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
    align-self: center;
    margin: 0 auto;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}